<template>
    <div class="banner">
      <el-carousel :interval="3000" type="card" height="250px">
        <el-carousel-item v-for="item in bannerList" :key="item">
          <el-image :src="item" style="height: 250px;"></el-image>
        </el-carousel-item>
      </el-carousel>
      <el-divider></el-divider>
    </div>
</template>

<script>
export default {
  name: 'Banner',
  data () {
    return {
      bannerList: [
        'static/images/banner/1.jpg',
        'static/images/banner/2.jpg',
        'static/images/banner/3.jpg'
      ]
    }
  }
}
</script>

<style scoped>
  .banner {
    border-top: 25px solid #f1f1f1;
    background-color: #f1f1f1;
  }
  .el-carousel__item h3 {
    color: #475669;
    font-size: 18px;
    opacity: 0.75;
    line-height: 300px;
    margin: 0;
  }

  .el-carousel__item:nth-child(2n) {
    background-color: #99a9bf;
  }

  .el-carousel__item:nth-child(2n+1) {
    background-color: #d3dce6;
  }
</style>



