<template>
  <div class="hello">
    <Banner></Banner>
    <div class="">
      <div class="aboutBox">
        <a href="about.html" class="aboutMore">MORE +</a>
        <div class="aboutTitle">
          <h1>Company Profile</h1>
          <span>ABOUT US</span></div>
        <section>
          CoreCabe with his own technical strength, integrity services, in the high-tech enterprises maintained a healthy, stable and rapid sustainable development.
          CoreCabe adhere to the market as the guidance, take development as the theme, science and technology as the power
          , to the concept of people-oriented, and constantly strengthen the economy turn management, formation of the perfect organization
          , reasonable structure, harmonious and orderly running flexible effective mechanism.
        </section>
      </div>
    </div>
    <!--产品推荐-->
    <div class="product_recommend">
      <div class="product_recommend_tab">
        <el-tabs v-model="tabActiveName" @tab-click="tabHandleClick" style="margin-left: 40px;">
          <el-tab-pane v-for="(item,index) in recommendProductsList" :key="item" :label="item" :name="index + 1 + ''" ></el-tab-pane>
        </el-tabs>
        <el-link type="primary" style="margin: -40px 30px 0px 0px; float: right;">More >></el-link>
      </div>
      <ProductListBox :product-list="productList"></ProductListBox>
    </div>
  </div>
</template>

<script>
import Banner from '@/components/common/Banner'
import ProductListBox from '@/components/common/ProductListBox'
export default {
  components: {
    ProductListBox,
    Banner
  },
  data () {
    return {
      tabActiveName: '1',
      recommendProductsList: [
        'C-AF',
        'c to c 100w',
        'C-MiniDP'
      ],
      productList_1: [
        { img: 'static/images/products/1/1.jpg',
          name: 'static/images/products/1/1.jpg'
        },
        { img: 'static/images/products/1/2.jpg',
          name: 'static/images/products/1/2.jpg'
        },
        { img: 'static/images/products/1/3.jpg',
          name: 'static/images/products/1/3.jpg'
        },
        { img: 'static/images/products/1/4.jpg',
          name: 'static/images/products/1/4.jpg'
        },
        { img: 'static/images/products/1/5.jpg',
          name: 'static/images/products/1/5.jpg'
        },
        { img: 'static/images/products/1/6.jpg',
          name: 'static/images/products/1/6.jpg'
        },
        { img: 'static/images/products/1/7.jpg',
          name: 'static/images/products/1/7.jpg'
        },
        { img: 'static/images/products/1/8.jpg',
          name: 'static/images/products/1/8.jpg'
        },
        { img: 'static/images/products/1/9.jpg',
          name: 'static/images/products/1/9.jpg'
        },
        { img: 'static/images/products/1/10.jpg',
          name: 'static/images/products/1/10.jpg'
        },
        { img: 'static/images/products/1/11.jpg',
          name: 'static/images/products/1/11.jpg'
        }
      ],
      productList_2: [
        { img: 'static/images/products/2/1.jpg',
          name: 'static/images/products/2/1.jpg'
        },
        { img: 'static/images/products/2/2.jpg',
          name: 'static/images/products/2/2.jpg'
        },
        { img: 'static/images/products/2/3.jpg',
          name: 'static/images/products/2/3.jpg'
        },
        { img: 'static/images/products/2/4.jpg',
          name: 'static/images/products/2/4.jpg'
        },
        { img: 'static/images/products/2/5.jpg',
          name: 'static/images/products/2/5.jpg'
        },
        { img: 'static/images/products/2/6.jpg',
          name: 'static/images/products/2/6.jpg'
        },
        { img: 'static/images/products/2/7.jpg',
          name: 'static/images/products/2/7.jpg'
        }
      ],
      productList_3: [
        { img: 'static/images/products/3/1.jpg',
          name: 'static/images/products/3/1.jpg'
        },
        { img: 'static/images/products/3/2.jpg',
          name: 'static/images/products/3/2.jpg'
        },
        { img: 'static/images/products/3/3.jpg',
          name: 'static/images/products/3/3.jpg'
        },
        { img: 'static/images/products/3/4.jpg',
          name: 'static/images/products/3/4.jpg'
        },
        { img: 'static/images/products/3/5.jpg',
          name: 'static/images/products/3/5.jpg'
        },
        { img: 'static/images/products/3/6.jpg',
          name: 'static/images/products/3/6.jpg'
        },
        { img: 'static/images/products/3/7.jpg',
          name: 'static/images/products/3/7.jpg'
        }
      ],
      productList: this.productList_1
    }
  },
  created () {
    this.productList = this.productList_1
  },
  methods: {
    tabHandleClick (tab, event) {
      let tabActive = this.tabActiveName
      if (tabActive === '1') {
        this.productList = this.productList_1
      } else if (tabActive === '2') {
        this.productList = this.productList_2
      } else if (tabActive === '3') {
        this.productList = this.productList_3
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}

.aboutBox{padding:15px;height:200px; overflow:hidden; margin:15px 0px 0px 0px;background:url(.././assets/aboutbg.jpg) no-repeat;background-size:cover;position:relative}
.aboutBox .aboutTitle{width:100%;overflow:hidden;margin-bottom:5px;}
.aboutBox .aboutTitle h1{font-size:16px;margin:0;color:#fff;display:inline;}
.aboutBox .aboutTitle span{font-size:14px;color:#fff;display:inline}
.aboutBox section{clear: both; padding: 0px 0px 0px 0px;font-size:12px;color:#fff;line-height:25px}
.aboutBox section *{margin:0;padding:0;border:0}
.aboutBox .aboutMore{position:absolute;right:15px;color:#fff;background:#000;font-size:12px;width:60px;text-align:center;}
.aboutBox .aboutMore:hover{background:#fff;color:#8bcf61}

.product_recommend {
  background: #f9f9f9;
  margin-top: 20px;
}
.image-slot {
  width: 100%;
}
.product_list_box {
  position: static;
  display: inline-block;
  margin: 0 10px 0 10px;
}
.product_div {
  float: left;
  width: 250px;
  height: 250px;
  border: 2px solid #f9f9f9;
}
.el-image :hover{
  cursor:pointer;
}
.product_card {
  margin: 15px 25px 10px 20px;
  width: 210px;
  height: 210px;
}
.product_image {
  width: 100%;
  height: auto;
}
.product_name {
  vertical-align:middle;
  display: grid;
  text-align: center;
}
</style>



