<template>
    <div>
      <el-menu
        :default-active="activeIndex"
        class="el-menu-demo"
        mode="horizontal"
        @select="handleSelect"
        background-color="#545c64"
        text-color="#fff"
        active-text-color="#ffd04b">
        <el-menu-item index="1"><router-link tag="div" to="/index">首页</router-link></el-menu-item>
        <el-submenu index="2">
          <template slot="title">产品中心</template>
          <el-menu-item v-for="item in menuItems" :key="item" :index="item">{{item}}</el-menu-item>
          <!--<el-submenu index="2-4">
            <template slot="title">选项4</template>
            <el-menu-item index="2-4-1">选项1</el-menu-item>
            <el-menu-item index="2-4-2">选项2</el-menu-item>
          </el-submenu>-->
        </el-submenu>
        <el-menu-item index="3"><router-link tag="div" to="/about">关于我们</router-link></el-menu-item>
        <el-menu-item index="4"><router-link tag="div" to="/contact">联系我们</router-link></el-menu-item>
      </el-menu>
    </div>
</template>

<script>
export default {
  props: {
    menuItems: {
      type: Array,
      default: function () {
        return [
          'menu-1',
          'menu-2',
          'menu-3'
        ]
      }
    }
  },
  data () {
    return {
      activeIndex: '1'
    }
  },
  created () {
    let currentPath = this.$route.path
    if (currentPath === '/index') {
      this.activeIndex = '1'
    } else if (currentPath === '/about') {
      this.activeIndex = '3'
    } else if (currentPath === '/contact') {
      this.activeIndex = '4'
    } else {
      this.activeIndex = this.menuItems[0]
    }
  },
  watch: {
    $route (to, from) {
      let currentPath = to.path
      if (currentPath === '/index') {
        this.activeIndex = '1'
      } else if (currentPath === '/about') {
        this.activeIndex = '3'
      } else if (currentPath === '/contact') {
        this.activeIndex = '4'
      } else {
        this.activeIndex = this.menuItems[0]
      }
    }
  },
  methods: {
    handleSelect (key, keyPath) {
      if (keyPath[0] === '2') {
        let currentPath = this.$route.path
        if (currentPath !== '/product') {
          this.$router.push(
            {name: 'ProductList', params: { keyPath: keyPath }}
          )
        }
      }
    }
  }
}
</script>

<style scoped lang="stylus" ref="stylesheet/stylus">
  .tab
    background-color dimgrey
    display: flex
    height: 44px
    line-height: 44px
    font-size: 14px
    .tab-item
      flex: 1
      text-align: center
      .tab-link
        padding-bottom: 5px
        color: #f5f5f5
      &.router-link-active
         .tab-link
           color: #00b5a4
           border-bottom: 4px solid #00b5a4
        &:hover
          background-color: #d9d9d9
          cursor:pointer
</style>



